<template>
  <div class="presentation card border-0 shadow-sm mb-4 bg-light">
    <h5 class="h6 pl-3 pt-3 mb-0">
      <span class="text-muted text-uppercase">Presentación</span>
    </h5>
    <address class="card-body p-3 m-0">
      <p class="p-0 mx-0 my-2" v-if="location">
        <span class="small font-weight-bold">Dirección</span><br />
        <span>{{ location }}</span>
      </p>
      <p class="p-0 mx-0 my-2" v-if="phone">
        <span class="small font-weight-bold">Teléfono</span><br />
        <span>{{ phone }}</span>
      </p>
      <p class="p-0 mx-0 my-2" v-if="mobile">
        <span class="small font-weight-bold">Celular</span><br />
        <span>{{ mobile }}</span>
      </p>
      <p class="p-0 pt-3 mx-0 my-2 border-top">
        <span v-if="cityCode"
          ><router-link
            :to="{
              name: 'UCity',
              params: {
                country: countrySlug,
                state: stateCode,
                city: cityCode
              }
            }"
            role="button"
            >{{ cityName }}</router-link
          ></span
        ><span v-if="stateCode"
          >,
          <router-link
            :to="{
              name: 'UState',
              params: { country: countrySlug, state: stateCode }
            }"
            role="button"
            >{{ stateCode }}</router-link
          ></span
        ><br v-if="cityCode || stateCode" />
        <span v-if="countryName"
          ><router-link
            :to="{ name: 'UCountry', params: { country: countrySlug } }"
            role="button"
            >{{ countryName }}</router-link
          ></span
        >
      </p>
    </address>
  </div>
</template>

<script>
export default {
  name: "Presentation",
  props: {
    city: Array,
    country: Array,
    location: String,
    mobile: String,
    phone: String,
    state: Array
  }
};
</script>

